import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { UserFavouriteService } from 'src/app/service/user-favourite.service';
declare var swal: any;

@Component({
  selector: 'app-user-favourite-add',
  templateUrl: './user-favourite-add.component.html',
})
export class UserFavouriteAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;

  productList:any=[];
  productImage='';
  showLoader = false;
  formErrors = {
    product_id: null,
    apierror: null
  };
  isEditing = false;
  editId = null;


  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private userfavouriteservice: UserFavouriteService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      product_id: [null],
      is_favourite: [true, Validators.compose([Validators.required])],
    });
  }

  ngOnInit() {

  }

  editObject() {
    this.loadingState = true;
    this.userfavouriteservice.getUserFavouriteById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
          this.addForm.patchValue({
            product_id: response.data.product_id,
            is_favourite:response.data.is_favourite
          });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('/favourite');
    });
  }

 
  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      if (this.isEditing) {
        this.userfavouriteservice.editUserFavourite(this.editId, formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/favourite');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('product_id')) {
                this.formErrors.product_id = obj.product_id;
              }

            });
          }
        });
      } else {
        this.userfavouriteservice.addUserFavourite(formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/favourite');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('product_id')) {
                this.formErrors.product_id = obj.product_id;
              }
            });
          }
        });
      }
    }
  }
}
